@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&display=swap');

@include keyframes(swing) {
  0% {
    transform: rotate(5deg); }
  100% {
    transform: rotate(-5deg); } }

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif;
  color: $gray01; }

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield; }

img {
  width: 100%;
  vertical-align: top; }

html {
  font-size: 20px; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  figure.logo {
    width: 145px; }
  @include rwd(768px) {
    padding: 10px 15px; } }



html {
  font-size: 20px;
  article {
    section.banner {
      .slogan {
        padding: 5px 0;
        text-align: center;
        background-color: $red01;
        figure.icon {
          width: 50px;
          @include dib; }
        p {
          padding: 0 1rem;
          font-size: 1.7rem;
          color: #fff;
          @include dib; }
        figure.btn {
          width: 223px;
          @include dib; } } }
    section.section01 {
      padding: 95px 0;
      background-color: #f7f2e9;
      .wrapper {
        width: 100%;
        padding: 60px 30px;
        margin: auto;
        box-sizing: border-box;
        background-color: #fff;
        font-size: 0;
        .container {
          max-width: 1200px;
          margin: auto;
          .box {
            @include dib;
            &:nth-child(1) {
              width: calc(100% - 500px); }
            &:nth-child(2) {
              width: 500px;
              box-sizing: border-box; }
            .item {
              width: 50%;
              padding: 0 15px;
              box-sizing: border-box;
              text-align: center;
              @include dib;
              figure.pic {
                max-width: 300px;
                margin: auto; }
              p.school-info {
                font-size: 0.9rem;
                font-weight: bold;
                span.num {
                  font-size: 1.6rem;
                  line-height: 0.8;
                  color: $red01; } } }
            p.txt {
              font-size: 1.1rem;
              line-height: 1.8;
              span.blue {
                font-size: 1.5rem;
                line-height: 1.2;
                color: $blue01; }
              span.green {
                font-size: 1.5rem;
                line-height: 1.2;
                color: $green01; }
              span.red {
                font-size: 1.8rem;
                line-height: 1;
                color: $red01; } }
            figure.btn {
              width: 330px;
              margin-top: 30px; } } } } }

    section.section02 {
      padding: 170px 0 50px;
      background-color: $gray02;
      .wrapper {
        max-width: 1260px;
        width: 95%;
        margin: auto;
        position: relative;
        background-color: #fff;
        box-shadow: 0px 0px 20px rgba(#000,.5);
        .title {
          @include titleStyle($red02,-8px,-8px);
          display: none;
          padding: 0.5rem 1.5rem;
          position: absolute;
          top: 0;
          left: 50%;
          z-index: 2;
          transform: translate(-50%,-80%); }
        .arrow {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          background-color: $red02;
          cursor: pointer;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          z-index: 2;
          &.disabled {
            background-color: $gray03;
            pointer-events: none; }
          p {
            font-size: 1.5rem;
            font-weight: 300;
            color: #fff;
            @include pstc5; }
          &#infoLeftArrow {
            left: 35px; }
          &#infoRightArrow {
            right: 35px; } }
        .carousel-box {
          margin-bottom: 45px;
          .owl-dots {
            text-align: center;
            position: relative;
            bottom: -45px;
            .owl-dot {
              width: 14px;
              height: 14px;
              margin: 0 5px;
              border: 2px solid #fff;
              border-radius: 50%;
              background-color: #fff;
              &.active {
                background-color: $red02; } } }
          .container {
            width: 100%;
            padding: 130px 110px 60px;
            box-sizing: border-box; } } }
      .remind {
        padding-top: 30px;
        font-size: 0.8rem;
        text-align: center; } }

    section.section04 {
      padding: 90px 0 110px;
      background-color: $green03;
      .wrapper {
        max-width: 1200px;
        width: calc(100% - 30px);
        padding: 100px 50px;
        margin: auto;
        box-sizing: border-box;
        background-color: #fff;
        position: relative;
        .title {
          @include titleStyle($green02,-10px,8px);
          padding: 1rem 1.5rem;
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%,-50%);
          p {
            color: $gray04;
            @include dib;
            span.green {
              color: $green02; } }
          figure.gift-pic {
            width: 75px;
            @include dib; } }
        .box {
          font-size: 0;
          .item {
            width: 50%;
            padding: 0 50px;
            box-sizing: border-box;
            text-align: center;
            display: inline-block;
            vertical-align: top;
            .item-title {
              padding: 10px 50px;
              margin-bottom: 30px;
              font-size: 1.5rem;
              color: #fff;
              background-color: $green02;
              text-align: center;
              box-sizing: border-box;
              display: inline-block; }
            .txt {
              min-height: 120px;
              margin-bottom: 30px;
              font-size: 1.05rem;
              text-align: left; }
            figure.pic {
              max-width: 340px;
              margin: auto;
              box-shadow: 5px 5px 7px rgba($gray01,.2); } } } } }

    section.section05 {
      background-color: $gray02;
      position: relative;
      .wrapper {
        padding: 110px 0;
        .title {
          @include titleStyle($orange01,-6px,-6px);
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%,-50%);
          p {
            span.orange {
              color: $orange01; } } }
        .box {
          max-width: 1500px;
          width: calc(100% - 30px);
          margin: auto;
          display: flex;
          .item {
            width: calc(50% - 40px);
            margin: 0 20px;
            background-color: #fff;
            .item-title {
              width: 350px;
              padding: 8px 0;
              margin: auto;
              font-size: 1.7rem;
              background-color: #fff;
              border: 2px solid $orange01;
              text-align: center;
              box-sizing: border-box;
              transform: translateY(-30%);
              span.orange {
                color: $orange01; } }
            .form-box {
              padding: 0 65px;
              .row {
                display: flex;
                margin-bottom: 20px;
                &.align-center {
                  align-items: center;
                  p {
                    padding-left: 30px; } }
                .row-label {
                  p {
                    width: 200px;
                    font-size: 1.2rem;
                    line-height: 3rem;
                    span.remark {
                      color: $orange01; } } }
                .row-content {
                  flex: 1;
                  input[type="text"],input[type="phone"] {
                    width: 100%;
                    padding: 0 1rem;
                    line-height: 3rem;
                    font-size: 1.2rem;
                    border: 1px solid #d4d1d1;
                    box-sizing: border-box; }
                  .radio-box {
                    line-height: 3rem;
                    input[type="radio"] {
                      position: absolute;
                      z-index: -1;
                      @include dib;
                      &:checked + p:after {
                        display: block; } }
                    p {
                      padding-left: 60px;
                      padding-right: 25px;
                      font-size: 1.3rem;
                      position: relative;
                      cursor: pointer;
                      @include dib;
                      @include before {
                        width: 38px;
                        height: 38px;
                        border: 1px solid $gray03;
                        border-radius: 50%;
                        position: absolute;
                        left: 0;
                        top: 0;
                        bottom: 0;
                        margin: auto; }
                      @include after {
                        display: none;
                        width: 24px;
                        height: 24px;
                        background-color: $red03;
                        border-radius: 50%;
                        position: absolute;
                        left: 8px;
                        top: 0;
                        bottom: 0;
                        margin: auto; } } } } }
              .canvas-box {
                padding-left: 200px;
                canvas {
                  width: 180px;
                  height: 40px; } } }
            figure.btn1 {
              width: 190px;
              margin: 40px auto; }
            .remind {
              padding-top: 60px;
              font-size: 1.25rem;
              text-align: center;
              span.red {
                color: $red03; } }
            figure.pic {
              width: 90%;
              margin: auto; }
            figure.btn2 {
              width: 190px;
              margin: 40px auto; } } } } } } }



